<template>
    <div id="shopcart">
        <el-table :data="tableData" >
            <el-table-column prop="goodsName" label="商品名称" width="180" >
            </el-table-column>
            <el-table-column prop="goodsPrice" label="商品单价" width="180">
            </el-table-column>
            <el-table-column prop="goodsCount" label="数量" >
            </el-table-column>
            <el-table-column  label="操作">
                <template slot-scope="scope">
                    <el-button @click="delSingleGoods(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div>
            <big>数量：{{totalCount}}</big>
            <big>金额：{{totalMoney}}</big>
        </div>
        <div>
            <el-button  size="big" @click="checkout" type="success">结账</el-button>
            <el-button  size="big" @click="delAllGoods" type="info">取消订单</el-button>
        </div>
    </div>
</template>


<script>
import bus from '../main.js'

export default {
    data() {
        return {
            tableData: [],//购物车列表
            totalCount:0,//总量
            totalMoney:0,//总价
        }
    },

    mounted:function(){
        // 设置高度
        var inorderWidth=parseInt(document.body.clientWidth)-90;
        document.getElementById("shopcart").style.width=inorderWidth+'px';

        
    },

    created:function(){
        var that = this;

        bus.$on('sendCount',function(ems){//接收总量
            that.totalCount = ems;
        });

        bus.$on('sendMoney',function(ems){//接收总价
            that.totalMoney = ems;
        })

        bus.$on('sendmenugoods',function(ems){//接收列表
            that.tableData = ems;
        })
    },

    methods:{
        // 计算总价和总量
        addAll:function(){
            this.totalMoney = 0;
            this.totalCount = 0;
            this.tableData.forEach(element => {
                if(element.goodsCount){
                    let menuCount = parseInt(element.goodsCount);
                    if(menuCount != 0){
                        this.totalMoney += menuCount*element.goodsPrice;
                        this.totalCount += menuCount
                    } 
                }
            });
        },
        
        //删除按钮
        delSingleGoods:function(goods){
            goods.goodsCount = 0;
            bus.$emit('sendShopcart',goods.goodsId);
            this.tableData = this.tableData.filter( element => element.goodsId !== goods.goodsId);
            bus.$emit('sendif',false);            
            this.addAll();
        },

        // 结账按钮
        checkout(){
            if(this.tableData.length){
                this.tableData = [];
                this.totalCount = 0;
                this.totalMoney = 0;
                this.$message({
                    message:'结账成功，感谢你为店里出了一份力！',
                    type:"success",
                });
                bus.$emit('sendif',false);
            } else{
                this.$message.error('不能空接，老板了解你急切的心情！');
            }
        },

        //取消按钮
        delAllGoods(){
            this.tableData=[];
            this.totalCount = 0;
            this.totalMoney = 0;
            bus.$emit('sendif',false);
        },
    }
}
</script>


<style>
#shopcart{
    position: relative;
    margin-left: 90px;
    font-size: 15px;
}
</style>



