<template>
    <div id="menu">
        <div >
            <!-- 头部 -->
            <el-row class="menu-head">
                <el-col :span="24" class="menu-head" >
                    <div class="menu-head-box">
                        <span >用餐人数:</span>
                        <el-input placeholder="" v-model="count" class="head" style="width:80px;"></el-input>
                        <el-button type="primary" >确定</el-button>
                    </div>
                    <div class="menu-head-search">
                        <el-input placeholder="请输入菜名" v-model="searchText" class="head" style="width:200px;"></el-input>
                        <el-button type="primary" >搜索</el-button>
                    </div>
                </el-col>
            </el-row> 
            <el-row>
                <!-- 导航栏 -->
                <el-col :span="6">
                    <div class="menu-list">
                         <el-menu
                            default-active="1"
                            class="el-menu-vertical-demo menu-list-nev"
                            @open="handleOpen"
                            @close="handleClose">
                            <el-menu-item index="1">
                                <i class="el-icon-location"></i>
                                <span slot="title">热卖</span>
                            </el-menu-item>
                            <el-menu-item index="2" >
                                <i class="el-icon-star-on"></i>
                                <span slot="title">套餐</span>
                            </el-menu-item>
                            <el-submenu index="3">
                                <template slot="title">
                                    <i class="el-icon-menu"></i>
                                    <span>主食</span>
                                </template>
                                <el-menu-item-group>
                                    <el-menu-item index="1-1">鸡翅/鸡排</el-menu-item>
                                    <el-menu-item index="1-2">汉堡</el-menu-item>
                                </el-menu-item-group>
                            </el-submenu>
                            
                            <el-menu-item index="4" >
                                <i class="el-icon-document"></i>
                                <span slot="title">小食</span>
                            </el-menu-item>
                            <el-menu-item index="5">
                                <i class="el-icon-setting"></i>
                                <span slot="title">饮品</span>
                            </el-menu-item>
                        </el-menu>
                    </div>
                </el-col>
                <el-col :span="18">
                    <div>
                        <!-- 商品栏 -->
                        <div class="goods-list" >
                            <ul v-model="menuGoods">
                                <li v-for="goods in menuGoods" @click="" class="goods">
                                    <div class="goods-img">
                                        <img :src=goods.goodsImg >
                                        <div class="goods-name">{{goods.goodsName}}</div>
                                    </div>
                                    <div class="goods-detail">
                                        <span>{{goods.goodsPrice}}元/份</span>
                                        <div class="goods-detail-btn">
                                            <el-input-number v-model="goods.goodsCount" @change="handleChange" :min="0" :max="99" label="描述文字" style="width:130px;"></el-input-number>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </el-col>
            </el-row> 
        </div>
    </div>
</template>


<script>
import bus from '../main.js'

export default {
    data(){
        return {
            count:'',//用餐人数
            searchText:'',//搜索键
            menuGoods:[//总列表货物
                {
                    goodsId:'001',
                    goodsName:'汉堡',
                    goodsPrice:101,
                    goodsCount:0,
                    goodsType:1,
                    goodsHot:false,
                    goodsImg:'https://f11.baidu.com/it/u=3419358752,715865344&fm=72'
                },
                {
                    goodsId:'002',
                    goodsName:'好好吃汉堡',
                    goodsPrice:130,
                    goodsCount:0,
                    goodsType:1,
                    goodsHot:false,
                    goodsImg:'https://f11.baidu.com/it/u=3419358752,715865344&fm=72'
                },
                {
                    goodsId:'003',
                    goodsName:'薯条',
                    goodsPrice:100,
                    goodsCount:0,
                    goodsType:2,
                    goodsHot:false,
                    goodsImg:'https://f11.baidu.com/it/u=3419358752,715865344&fm=72'
                },
                {
                    goodsId:'004',
                    goodsName:'鸡米花',
                    goodsPrice:19,
                    goodsCount:0,
                    goodsType:2,
                    goodsHot:true,
                    goodsImg:'https://f11.baidu.com/it/u=3419358752,715865344&fm=72'
                },
            ],
            typeGoods0:[],//显示列表
            orderGoodsList:[],//购物车列表
            totalMoney:0,//总价
            totalCount:0 ,//总量
            istrue: true,//判断是否需要初始化
        }
    },

    

    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },//导航下拉

        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },//导航栏关闭

        //更新购物车；
        orderGoods:function(){
            this.orderGoodsList=[];
            this.menuGoods.forEach(element => {//将count不为0的商品加入购物车；
                if(element.goodsCount){
                    let menuCount = parseInt(element.goodsCount);
                    if(menuCount != 0){
                        this.orderGoodsList.push(element);
                    } 
                }
            });
        },

        // 计算总价和总量
        addAllMoney:function(){
            this.totalMoney = 0;
            this.totalCount = 0;
            this.menuGoods.forEach(element => {
                if(element.goodsCount){
                    let menuCount = parseInt(element.goodsCount);
                    if(menuCount != 0){
                        this.totalMoney += menuCount*element.goodsPrice;
                        this.totalCount += menuCount
                    } 
                }
            });
        },

        // 加减按钮
        handleChange(value){
            this.addAllMoney();
            var str = JSON.stringify(this.menuGoods);
            this.orderGoods();
        },
    },



    mounted:function(){//设置高度为页面高度；
        var orderWidth=parseInt(document.body.clientWidth)-90;
        document.getElementById("menu").style.width=orderWidth+'px';
        // 加载菜单
        this.menuGoods.forEach(element => {
            if(element.goodsHot){
                this.typeGoods0.push(element);
            }
        });

        // 在购物车页改变的购物车列表数据
        var that = this;
        bus.$on('sendShopcart',function(ems){//接收总量
            that.menuGoods.forEach(element => {
                if(element.goodsId === ems){
                    element.goodsCount = 0; 
                }
            });
        });

        //如果传值为FALSE则，用istrue接收，触发watch判断是否初始化数据；
        bus.$on('sendif',function(ems){//接收总量
            that.istrue = ems;
            console.log(ems)
        });


    },

        

    watch:{
        // 监控数据总量的变化，传值给兄弟组件menu；
        totalCount:function(newVal,oldVal){
            bus.$emit('sendCount',this.totalCount);//传递总量；
            bus.$emit('sendMoney',this.totalMoney);//传递总价；
            bus.$emit('sendmenugoods',this.orderGoodsList);//传递购物车列表；
        },

        //数据初始化
        istrue:function () {
            if (!this.istrue) {
                this.menuGoods.forEach(element => {
                    element.goodsCount = 0;  
                });
                this.totalCount = 0;
                this.totalMoney = 0;
                this.orderGoods();
                this.istrue = true;
            }
            console.log(1)
        }
    }
}
</script>


<style>
#menu{
    position: relative;
    margin-left: 90px;
    font-size: 15px;
}
.menu-head{
    height: 80px;
    background: #ccc;
    position: relative;             
}
.menu-head-box{
    width: 300px;
    margin:20px 60px;
}
.menu-head-search{
    position: absolute;
    top:20px;
    right: 100px;
}
.goods-input{
    width:30px;
    height: 30px;
    text-align: center;
}
.goods-list li{
    list-style: none;
}
.goods{
    width:300px;
    height: 200px;
    float: left;
    text-align: center;
    margin:20px 0;
}
.goods-img{
    position: relative;
    height: 150px;
    width: 250px;
    margin: 0 auto;
}
.goods-img img{
    width: 100%;
    height: 100%;
}
.goods-name{
    background: #ccc 0.4;
    position: absolute;
    bottom: 0;
    width:100%;
    height: 30px;
}
.goods-detail{
    position: relative;
}
.goods-detail>span{
    position:absolute;
    left:40px;
    top:20px;
    color: #1122ef;
    font-size: 18px;
}
.goods-detail-btn{
    position: absolute;
    right: 40px;
    top:10px;
    text-align: center;
    
}
</style>



