  <template>
    <div id="app">
      <left></left>
      <keep-alive>
        <router-view v-model="routerIn" v-if="routerIn"></router-view>
      </keep-alive>
    </div>
  </template>

  <script>
  import left from '@/components/common/left'
  export default {
    name: 'App',

    data(){
      return {
        routerIn:true,
      }
    },

    components:{
      left
    },

    methods:{
      
    }
  }
  </script>

  <style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  </style>



